* {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body {
	padding: 60px;
}

input {
	color: gray;
	font-size: 1.2rem;
	width: 100%;
}

.card {
	align-content: center;
	border: 1px solid gray;
	border-radius:6px;
	margin-bottom: 10px;
	margin-right: 10px;
	overflow: hidden;
	-webkit-border-radius:6px;
	-moz-border-radius:6px;
	-ms-border-radius:6px;
	-o-border-radius:6px;
}

.card p {
	padding: 5px;
	text-align: center;
}

.card img {
	max-height: 170px;
}

.card-grid {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}